











































  import Vue from 'vue'
  import {Component, Watch} from 'vue-property-decorator'
//  import server from '../api'
  import axios from 'axios'
  import {stringify} from 'qs'
  @Component
  export default class GeekLogin extends Vue {
    name = 'GeekLogin'
    public password: string = ''
    public account: string = ''
    public passwordOK = true
    public accountOK = true


    @Watch('password')
    public passwordChange(newValue) {
      this.passwordOK = true
    }

    @Watch('account')
    public accountChange(newValue) {
      this.accountOK = true
    }

    public async login() {
      if(!this.checkArgs()) {
        return
      }
      const {data} = await axios.post('http://10.1.65.33:81/login', stringify({
        account: this.account,
        password: this.password
      }))
      if(data.code != 200) {
        Vue.toasted.show(data.msg, {
          theme: 'bubble',
          position: 'bottom-center',
          duration : 2000,
        });
      } else {
        Vue.toasted.success('欢迎回来', {
          theme: 'bubble',
          position: 'bottom-center',
          duration : 2000,
        });
        this.$router.push('/home')
      }
    }

    public checkArgs() {
      return (
        this.account != '' &&
        this.password != '' &&
        this.accountOK &&
        this.passwordOK
      )
    }
  }



